import React, { FC } from 'react'
import clsx from 'clsx'
import { AccentButtonProps } from './types'
import './AccentButton.scss'
import { AccentCard } from '../../cards/AccentCard'
import { Skeleton } from '../../other/Skeleton'
import { AccentCardProps } from '../../cards/AccentCard/types'
import { SkeletonProps } from '../../other/Skeleton/types'

const AccentButton: FC<AccentButtonProps> = ({
  isLoading = false,
  isAccented = true,
  title = '',
  className,
  disabled = false,
  onClick,
  size = 'S',
  children = null,
  icons = [],
}) => {
  const accentButtonProps: Partial<AccentButtonProps> = {
    disabled: disabled || isLoading,
    onClick,
    className: clsx(
      'AccentButton',
      className,
      clsx({
        'AccentButton_Shadow-default': !isAccented,
        'AccentButton_Shadow-accent': isAccented,
      }),
    ),
  }
  const skeletonProps: SkeletonProps = {
    isLoading,
    animation: 'wave',
    variant: 'text',
  }
  const accentCardProps: AccentCardProps = {
    isAccented,
    title,
    className: 'AccentButton__AccentCard',
    size,
    children,
    icons,
  }

  return (
    <button {...accentButtonProps}>
      <Skeleton {...skeletonProps}>
        <AccentCard {...accentCardProps} />
      </Skeleton>
    </button>
  )
}

export default AccentButton
